<template>
    <div class="user-fans-box">
        <div class="header">
            <div class="top-bar row bg-white md">
                <div class="bar-item row-center" :class="{'item-active': active == 'all'}" @click="changeTab('all')">全部粉丝</div>
                <div class="bar-item row-center" :class="{'item-active': active == 'first'}" @click="changeTab('first')">一级推广</div>
                <div class="bar-item row-center" :class="{'item-active': active == 'second'}" @click="changeTab('second')">二级推广</div>
            </div>
            <van-search
                shape="round"
                v-model="keyword"
                show-action
                background="white"
                placeholder="请输入搜索关键词"
                @search="onSearch"
            >
                <template #action>
                    <div @click="onSearch">确定</div>
                </template>
            </van-search>
            <div class="sort-bar row">
                <div class="sort-bar-item row-center nr" @click="sortStatusChange(0)">
                    <div :class="{'item-active': sortType == 0}">团队排序</div>
                    <div class="column">
                        <trigonometry direction="up" :color="fansSort == 'asc' ? primaryColor : '#585858'" size="small"/>
                        <trigonometry :color="fansSort == 'desc' ? primaryColor : '#585858'" size="small" />
                    </div>
                </div>
                <div class="sort-bar-item row-center nr" @click="sortStatusChange(1)">
                    <div :class="{'item-active': sortType == 1}">金额排序</div>
                    <div class="column">
                        <trigonometry direction="up" :color="moneySort == 'asc' ? primaryColor : '#585858'" size="small"/>
                        <trigonometry :color="moneySort == 'desc' ? primaryColor : '#585858'" size="small" />
                    </div>
                </div>
                <div class="sort-bar-item row-center nr" @click="sortStatusChange(2)">
                    <div :class="{'item-active': sortType == 2}">订单排序</div>
                    <div class="column">
                        <trigonometry direction="up" :color="orderSort == 'asc' ? primaryColor : '#585858'" size="small" />
                        <trigonometry :color="orderSort == 'desc' ? primaryColor : '#585858'" size="small" />
                    </div>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="data-null column-center" v-if="isEmpty">
                <img class="img-null" src="@A/images/null_order.png" alt="">
                <div class="muted">暂无相关数据...</div>
            </div>
            <div v-else class="card-box">
                <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了"
                    @load="$getUserFans"
                >
                    <div class="card-item row-between" v-for="item in fansObject" :key="item.id">
                        <div class="row">
                            <div class="card-img">
                                <van-image :src="item.avatar" round width="100%" height="100%" />
                            </div>
                            <div class="fans-info ml10">
                                <div class="fans-name row">{{item.nickname}}</div>
                                <div class="row lighter mt5">
                                    <div>{{item.mobile}}</div>
                                    <div class="ml20">{{item.create_time}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="column xs">
                            <div class="msg"><span class="primary">{{item.fans_team}} </span>人</div>
                            <div class="mt5 msg"><span>{{item.fans_order}} </span>单</div>
                            <div class="mt5 msg"><span>{{item.fans_money}} </span>元</div>
                        </div>
                    </div>
                </van-list>
            </div>
        </div>
    </div>
</template>

<script>
import {getUserFans} from "@API/user"
import Trigonometry from "@C/Trigonometry"
import {FansType, SortType} from "@U/type"
export default {
    name: "userFans",
    components: {
        Trigonometry
    },
    data() {
        return {
            loading: true,
            finished: false,
            isEmpty: false,
            active: FansType.ALL,
            sortType: 0,
            keyword: "",
            fansSort: SortType.ASC,
            moneySort: SortType.NONE,
            orderSort: SortType.NONE,
            fansObject: [],
            page: 1
        }
    },
    methods: {
        $getUserFans() {
            const {keyword, fansSort, moneySort, orderSort} = this;
            const data = {
                type: this.active,
                keyword,
                fans: fansSort,
                money: moneySort,
                order: orderSort
            };
            this.loading = true;
            if(this.finished) return;
            getUserFans(data).then(res => {
                if(res.code == 1) {
                    this.loading = false;
                    let {more, list} = res.data;
                    this.fansObject.push(...list);
                    this.page ++;
                    if(!more) {
                        this.finished = true;
                    }
                    if(this.fansObject.length <= 0) {
                        this.isEmpty = true;
                    }
                }
            })
        },

        onSearch() {
            this.clearStatus();
            console.log('clear')
            this.$getUserFans();
        },
        
        changeTab(active) {
            this.active = active;
            this.clearStatus();
            this.$getUserFans();
        },
        
        // 清理状态
        clearStatus() {
            this.fansObject = [];
            this.page = 1;
            this.finished = false;
            this.loading = false;
        },

        // 排序状态更改
        sortStatusChange(num) {
            this.sortType = num;
            switch(num) {
                case 0:
                    // 团队排序
                    if(this.fansSort == SortType.DESC || this.fansSort == SortType.NONE) {
                        this.fansSort = SortType.ASC;
                        this.moneySort = SortType.NONE;
                        this.orderSort = SortType.NONE;
                    }
                    else if(this.fansSort == SortType.ASC){
                        this.fansSort = SortType.DESC;
                        this.moneySort = SortType.NONE;
                        this.orderSort = SortType.NONE;
                    }
                    break;
                case 1:
                    // 金额排序
                    if(this.moneySort == SortType.DESC || this.moneySort == SortType.NONE) {
                        this.moneySort = SortType.ASC
                        this.fansSort = SortType.NONE;
                        this.orderSort = SortType.NONE;
                    }
                    else if(this.moneySort == SortType.ASC){
                        this.moneySort = SortType.DESC;
                        this.fansSort = SortType.NONE;
                        this.orderSort = SortType.NONE;
                    }
                    break;
                case 2:
                    // 订单排序
                    if(this.orderSort == SortType.DESC || this.orderSort == SortType.NONE) {
                        this.orderSort = SortType.ASC;
                        this.moneySort = SortType.NONE;
                        this.fansSort = SortType.NONE;
                    }
                    else if(this.orderSort == SortType.ASC){
                        this.orderSort = SortType.DESC;
                        this.moneySort = SortType.NONE;
                        this.fansSort = SortType.NONE;
                    }
                    break;
            }
            this.clearStatus();
            this.$getUserFans();
        }
    },
    created() {
        this.$getUserFans();
    }
}
</script>

